.OurServices {
  max-width: 1200px;
  width: 100%;
  height: 200vh;
  font-family: "Montserrat Regular";
  &:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 200vh;
    background-color: deepskyblue;
    clip-path: polygon(0 78%, 100% 35%, 100% 100%, 0 100%); } }

.box {
 position: relative; }

.OurServices__context {
  position: relative;
  width: 100%;
  opacity: 1;
  &__title h1 {
    text-align: center; } }


.columns {
  margin: 30px 40px;
  display: flex;
  justify-content: space-between;
  & h1,hr {
    text-align: center; } }


.column {
  width: 20%; }


.laptop {
  position: relative;
  display: flex;
  justify-items: flex-start;
  &__content {
    width: 400px;
    text-align: center;
    margin: 20px;
    padding: 50px 10px; }
  &__img {
    position: relative;
    padding: 20px; }
  & .lap__img {
    width: 600px; } }

.tablet {
  display: flex;
  justify-items: flex-end;
  padding-bottom: 50px;
  &__content {
    text-align: center;
    width: 600px;
    padding: 80px 30px; }
  &__img {
    width: 500px;
    padding-left: 30px; } }


.boy {
  position: relative;
  display: flex;
  max-height: 675px;
  background-color: #fff;
  &__img {
    height: 100%;
    width: 600px; }
  &__text {
    font-family: "Montserrat Regular";
    text-align: center;
    justify-content: center;
    margin: 100px 20px; }
  > h1 {
    font-size: 6em;
    font-family: "Montserrat Regular"; } }


.FourBlocks {
  position: relative;
  display: flex;
  & div {
    height: 430px;
    width: 25%;
    padding: 40px 0;
    justify-content: center; }
  &__1 {
    background-image: url("../img/b1.png"); }
  &__2 {
    background-image: url("../img/b2.png"); }
  &__3 {
    background-image: url("../img/b3.png"); }
  &__4 {
    background-image: url("../img/b4.png"); }
  & div > p {
    color: #fff;
    text-align: center;
    padding-top: 5em;
    font-family: "Montserrat Regular";
    font-size: 27px; } }


.CheckOur__title {
  text-align: center; }

.Personal {
  width: 100%;
  padding: 30px 0;
  display: flex;
  justify-content: space-evenly;
  & ul {
    width: 25%; }
  & div {
    padding: 0 30px;
    width: 80px;
    text-align: center; } }



